.hero {
  &__container {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__text {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__title {
    margin: 0;
    max-width: 580px;

    font-weight: 700;
    font-size: 60px;
    line-height: 120%;
    color: var(--dark-color);
  }

  &__descr {
    margin: 0;
    max-width: 380px;


    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--dark-color);
  }
}
