.footer {
  padding-top: 56px;
  padding-bottom: 24px;
  background: var(--dark-color);

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;
  }

  &__col {
    grid-column: 6 span;
  }

  &__logo {
  }

  &__col-flex {
    display: flex;
    gap: 56px;
  }

  &__list {
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--light-color);
  }

  &__col-full {
    grid-column: 12 span;
    margin-top: 70px;
  }

  &__copyright {
    display: block;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    text-align: center;
    color: var(--light-color);
  }
}
.nav {
  &__list {
  }

  &__list-footer {
    display: block;
    columns: 2;
    max-width: 343px;
    padding-top: 0;
  }

  &__item {
  }

  &__item--footer {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__link {
  }

  &__link--footer {
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--light-color);
  }
}
