.news {
  &__container {
  }

  &__title {
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;
  }

  &__card {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: 380px;
    height: 100%;
    min-height: 478px;
    background: var(--light-gray);
    grid-column: 4 span;
  }
}
.card {
  &__image {
    display: block;
    height: auto;
    max-width: 100%;
  }

  &__name {
    overflow: hidden;
    margin: 0 0 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: var(--dark-color);
  }

  &__text-inner {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: flex-start;
    padding: 16px 16px 24px;
  }

  &__text-bottom {
    @include flex-all-sb;

    margin-top: auto;
    width: 100%;
  }

  &__link {
    margin: 0;
  }

  &__data {
  }
}
