.header {
  padding-bottom: 40px;
  &__container {
  }

  &__top {
    @include flex-all-sb;
    padding: 35px 0;
    border-bottom: 1px solid var(--gray-color);
  }

  &__logo {
  }

  &__right {
    display: flex;
    align-items: center;
    gap: 20px;

    text-align: right;
  }

  &__email {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    min-width: 180px;
  }

  &__phone {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    min-width: 180px;
  }

  &__caption {
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    color: var(--dark-color);
  }

  &__link {
    border-bottom: 1px solid currentColor;
    font-weight: 600;
    font-size: 20px;
    text-align: right;
    color: var(--accent-color);
  }

  &__nav {
  }
}

.nav {
  &__list {
    @include flex-all-sb;
    padding-top: 16px;
    gap: 44px;
  }

  &__item {
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--dark-color);
  }

  &__svg {
    width: 16px;
    height: 16px;
    color: var(--accent-color);
  }
}
