.products {
  &__container {
  }

  &__title {
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 25px;
    column-gap: 20px;
  }
}

.articles {
  &__card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-column: 4 span;
    padding: 32px 24px;
    height: 100%;
    min-height: 435px;
    background: var(--light-gray);
  }

  &__image {
    max-height: 200px;
  }

  &__subtitle {
    margin: 0;
    overflow: hidden;
    margin: auto 0 12px;
    font-weight: 600;
    font-size: 28px;
    line-height: 120%;
    color: var(--dark-color);
  }

  &__link {
    margin: 0;

    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--accent-color);
  }

  &__span {
  }
}
